#pagetop {
  display: none;
  position: fixed;
  bottom: 154px;
  right: 36px;
  z-index: 99;
}

@media screen and (max-width: 767px) {
  #pagetop {
    right: 10px;
  }
}

#pagetop a {
  background: #1c72c9;
  border: 2px solid #1c72c9;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 35px;
  height: 35px;
  border-radius: 50%;
  transition: all .3s ease-in-out;
}

#pagetop a span {
  position: relative;
  top: -4px;
  left: 0;
}

#pagetop a span:before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -11px;
  height: 7px;
  width: 7px;
  border-bottom: 3px solid #fff;
  border-left: 3px solid #fff;
  transform: translateX(-50%) rotate(-227deg);
}

@media screen and (max-width: 767px) {
  #pagetop a span:before {
    border-width: 2px;
  }
}

@media screen and (min-width: 768px) {
  #pagetop a:hover {
    opacity: 1;
    background: linear-gradient(35deg, #43cea2 5%, #185a9d 65%);
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.5);
    color: #fff;
  }
  #pagetop a:hover span:before {
    border-color: #fff;
