/* mainvisual
------------------------------------------------------------*/
.mainvisual {
	position: relative;
	&_img {
		height: 100vh;
		width: 100%;
		@include sp() {
			object-fit: contain;
			height: auto;
		}
	}

	&_team {
		position: absolute;
		bottom: 50px;
		left: 100px;
		text-transform: capitalize;
		color: #ffffff;
		font-size: 4.5rem;
		text-shadow: 1px 1px;
		@include sp() {
			font-size: 2.4rem;
			left: 25px;
			bottom: 15px;
		}
	}
}
