/*
Open Sans: font-family: 'Open Sans', sans-serif;
----------------------------------------------------*/
@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Open+Sans&display=swap');
/*
Noto Sans CJK JP
----------------------------------------------------*/
@font-face {
	font-family: "NotoSansCJKjp";
	src: local('Noto Sans CJK JP Thin'), local('NotoSansCJKjp-Thin');
	src: url(../font/NotoSansCJKjp-Thin.woff2) format("woff2");
	src: url(../font/NotoSansCJKjp-Thin.woff) format("woff");
	src: url(../font/NotoSansCJKjp-Thin.otf) format("opentype");
	font-style: normal;
	font-weight: 100;
}

