/* フォント
-------------------------------------------*/
@mixin yumin() {
	font-family: "游明朝", YuMincho, "ヒラギノ明朝 ProN W3", "Hiragino Mincho ProN", "HG明朝E", "ＭＳ Ｐ明朝", serif;
}

@mixin yugot() {
	font-family: "游ゴシック", YuGothic, "ヒラギノ角ゴ Pro", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
}

@mixin open-sans($w: '') {
	font-family: 'Open Sans', sans-serif;
	@if $w == li {
		font-weight: 300;
	} @else if $w == re {
		font-weight: 400;
	} @else if $w == se {
		font-weight: 600;
	} @else if $w == bo {
		font-weight: 700;
	} @else if $w == ex {
		font-weight: 800;
	}
}

@mixin noto-sans-cjk-jp($w: '') {
	//font-family: 'NotoSansCJKjp', sans-serif;
	@if $w == th {
		//Thin
		font-weight: 100;
	} @else if $w == li {
		//Light
		font-weight: 200;
	} @else if $w == de {
		//DemiLight
		font-weight: 300;
	} @else if $w == re {
		//Regular
		font-weight: 400;
	} @else if $w == me {
		//Medium
		font-weight: 500;
	} @else if $w == bo {
		//Bold
		font-weight: 700;
	} @else if $w == bl {
		//Black
		font-weight: 900;
	}
}

/* breakpoint
-------------------------------------------*/
@mixin sm {
	// <- 480px
	@media screen and (max-width: #{$breakpoint0}) {
		@content;
	}
}

@mixin sp {
	// <- 767px
	@media screen and (max-width: #{$breakpoint1}) {
		@content;
	}
}

@mixin pad {
	// <- 1060px
	@media screen and (max-width: #{$breakpoint2}) {
		@content;
	}
}

@mixin pc {
	// 1061px ->
	@media screen and (min-width: #{($breakpoint2 + 1px)}) {
		@content;
	}
}

@mixin min-w($breakpoint) {
	@media screen and (min-width: #{$breakpoint}) {
		@content;
	}
}

@mixin max-w($breakpoint) {
	@media screen and (max-width: #{$breakpoint}) {
		@content;
	}
}

@mixin ie {
	@media all and (-ms-high-contrast: none) {
		@content;
	}
}

/* Only use hover on PC
-------------------------------------------*/
@mixin hover {
	&:hover {
		// 768px ->
		@media screen and (min-width: #{($breakpoint1 + 1px)}) {
			@content;
		}
	}
}

/* Calculate letter-spacing photoshop
-------------------------------------------*/
@mixin letter-spacing($size, $tracking) {
	letter-spacing: ($size * $tracking / 1000);
}

/* Clearfix
-------------------------------------------*/
@mixin clear {
	*zoom: 1;
	&:after {
		display: block;
		content: "";
		clear: both;
	}
}

/* Other
-------------------------------------------*/
@mixin shadow {
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
}
