.c-list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &_item {
        width: 33.33%;
        padding: 0 20px;
        @include sp() {
            width: 100%;
            margin-bottom: 30px;
        }
    }

    &_name {
        font-size: 1.8rem;
        text-transform: capitalize;
        text-align: center;
        margin-top: 10px;
    }

    &_img {
        width: 100%;
        height: 30rem;
        img {
            width: 100%;
            height: 100%;
            border-radius: 50%;
            object-fit: cover;
        }
    }
    &_intro {
        text-align: justify;
    }
}