/*------------------------------------------------------------
title
------------------------------------------------------------*/
.c-title01 {
	font-size: 4.0rem;
	position: relative;
	padding-left: 20px;
	display: inline-block;
	font-weight: 600;
	text-transform: uppercase;
	margin-bottom: 30px;
	
	&::before {
		content: "";
		width: 10px;
		height: 100%;
		position: absolute;
		background-color: #cccccc;
		top: 0;
		left: 0;
	}
}

/* c-title02
------------------------------------------------------------*/
.c-title02{
	margin-bottom: 23px;
	font-size: 2.2rem;
	letter-spacing: 0.1rem;
	color: $color2;
	line-height: 1.5;
	font-weight: 400;
	position: relative;
	z-index: 1;
	display: inline-block;

	@include sp{
		margin-bottom: 15px;
		font-size: 2rem;
	}

	&:after{
		position: absolute;
		content: '';
		left: 0;
		background-color: $color1;
		height: 16px;
		width: 100%;
		bottom: -2px;
		z-index: -1;

		@include sp{
			height: 12px;
			bottom: 1px;
		}
	}
}

/* c-title03
------------------------------------------------------------*/
.c-title03{
	margin-bottom: 22px;

	&_jp{
		font-size: 2.2rem;
		line-height: 1.5;
		margin-left: 14px;
		letter-spacing: -0.07rem;
	}

	.c-title02{
		margin-bottom: 0;
	}
}
