.c-form {
    .c-input {
        width: 100%;
        border: 1px solid #ccc;
        padding: 10px 0 10px 10px;
        margin-bottom: 10px;
        box-sizing: border-box;
        font-size: 1.6rem;
    }
    textarea {
        margin-top: 10px;
        width: 100%;
        padding: 10px;
        box-sizing: border-box;
        font-size: 1.6rem;
    }
}