/*------------------------------------------------------------
header
------------------------------------------------------------*/
#header{
	background-color: #333;
	position: fixed;
	top: 0px;
	width: 100%;
	display: block;
	transition: top 0.3s;
	z-index: 999;
	@include sp() {
		background-color: unset;
	}
	#gnav {
		width: 100%;
		> ul {
			text-transform: uppercase;
			display: flex;
			justify-content: flex-end;
			padding: 10px 0;
			@include pad() {
				display: flex !important;
			}
			@include sp() {
				flex-direction: column;
				align-items: center;
				text-align: justify;
				padding: 40px 0;
				display: none !important;
			}
			
			li {
				margin-right: 35px;
				z-index: 999;
				color: #ffffff;
				font-size: 2.4rem;
				@include sp() {
					padding: 10px 0;
				}
				&:hover {
					opacity: 0.5;
				}
			}
		}
	}
}

