.c-box {
    width: 100%;
    &_item {
        margin-bottom: 20px;
        display: flex;
        flex-wrap: wrap;
        @include sp() {
            padding: 15px 0;
            margin-bottom: 0px;
        }
    }
    &_left {
        width: 50%;
        padding: 0 20px;
        @include sp() {
            width: 100%;
            padding-bottom: 20px;
        }
    }
    
    &_right {
        width: 50%;
        max-height: 250px;
        padding: 0 20px;
        @include sp() {
            width: 100%;
            margin-bottom: 20px;
        }
        img {
            height: 100%;
            width: 100%;
        }
    }
}

.focused {
    opacity: 0.3 !important;
}

.particles-js-canvas-el {
    position: absolute;
    top: 0;
    left: 0;
}

.flex-table {
    display: table;
}

.flex-reverse {
    flex-flow: wrap-reverse;
}


:root {
    --screen-width:     320px;
    --screen-height:    560px;
    --header-bg-color:  #673AB7;
    --splash-bg-color:  #368887;
  }
  
// calculate a circle's circumference based on radius
@function circumference($r){
    $pi: 3.141592653;
    @return 2*$pi*$r;
}

.nav {
    @include sp() {
        height: 0;
    }
    // Toggle Button
    &__toggle {
        background-color:rgba(0,0,0,.5) !important;
        display:inline-block;
        position:absolute;
        z-index:10;
        padding:0;
        border:0;
        background:transparent;
        outline:0;
        right:15px;
        top:15px;
        cursor:pointer;
        border-radius:50%;
        transition:background-color .15s linear;
        
        &:hover,
        &:focus {
            background-color:rgba(0,0,0,.5);
        }
    }
    
    &__menu {
        display:flex;
        flex-direction:column;
        // justify-content:center;
        height:var(--screen-height);
        position:relative;
        z-index:5;
        visibility:hidden;
        margin-top: 70px;
    }
    
    &__item {
      opacity:0;
      transition: all .3s cubic-bezier(0.000, 0.995, 0.990, 1.000) .3s;
    }
    @for $i from 1 through 5 {
      &__item:nth-child(#{$i}){
        transform:translateY(-40px * $i);
      }
    }
    
    &__link {
      color:white;
      display:block;
      text-align:center;
      text-transform:uppercase;
      letter-spacing:5px;
      font-size:2.6rem;
      font-family: 'Open Sans';
      text-decoration:none;
      padding:1rem;
      
      &:hover,
      &:focus {
        outline:0;
        background-color:rgba(0,0,0,0.2);
      }
    }
  }
  
  
  // ---------------------------
  // SVG Menu Icon
  // ---------------------------
  
  .menuicon {
    display:block;
    cursor:pointer;
    color: white;
    
    transform:rotate(0deg);
    transition: .3s cubic-bezier(0.165, 0.840, 0.440, 1.000); 
    
    &__bar,
    &__circle {
      fill:none;
      stroke: currentColor;
      stroke-width:3;
      stroke-linecap:round;
    }
    &__bar {
      transform: rotate(0deg);
      transform-origin:50% 50%;
      transition: transform .25s ease-in-out;
    }
    &__circle {
      transition: stroke-dashoffset .3s linear .1s;
      stroke-dashoffset:circumference(23); // 23 is the <circle>'s radius
      stroke-dasharray:circumference(23);
    }
}


.splash {
    position:absolute;
    top:40px;
    right:40px;
    width: 1px;
    height: 1px;
    
    &::after {
      content:"";
      display:block;
      position:absolute;
      border-radius:50%;
      background: linear-gradient(35deg, #43cea2 20%, #185a9d 52%);
      box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.5);
      // screen diameter can be 142vmax at most,
      // circle needs to be twice that size to cover it
      width:284vmax;
      height:284vmax;
      top:-142vmax;
      left:-142vmax;
      
      transform: scale(0);
      transform-origin:50% 50%;
      transition: transform .5s cubic-bezier(0.755, 0.050, 0.855, 0.060);
      
      // will-change tells the browser we plan to 
      // animate this property in the near future
      will-change:transform;
    }
  }
  
  
  // ---------------------------
  // Active State
  // ---------------------------
  
  .nav:target,
  .nav--open {
    
    //scale the background circle to full size
    > .splash::after {
      transform:scale(1);
    }
    
    //animate the menu icon
    .menuicon {
      color:white;
      transform:rotate(180deg);
      
      &__circle {
        stroke-dashoffset:0;
      }
      &__bar:nth-child(1),
      &__bar:nth-child(4) {
        opacity:0;
      }
      &__bar:nth-child(2) {
        transform: rotate(45deg);
      }
      &__bar:nth-child(3) {
        transform: rotate(-45deg);
      }
    }
    
    //show the nav items
    .nav {
       
      &__menu {
        visibility:visible;
      }
    
      &__item {
        opacity:1;
        transform:translateY(0);
      }
    }
  }
  
.height_textarea {
    max-height: initial !important;
}

#pagetop {
    display: none;
    position: fixed;
    bottom: 154px;
    right: 36px;
    z-index: 99;

    @include sp {
        right: 10px;
    }

    a {
        background: $color2;
        border: 2px solid $color2;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 35px;
        height: 35px;
        border-radius: 50%;
        transition: all .3s ease-in-out;

        span {
            position: relative;
            top: -4px;
            left: 0;

            &:before {
                content: "";
                position: absolute;
                left: 50%;
                bottom: -11px;
                height: 7px;
                width: 7px;
                border-bottom: 3px solid #fff;
                border-left: 3px solid #fff;
                transform: translateX(-50%) rotate(-227deg);

                @include sp{
                    border-width: 2px;
                }
            }
        }

        @include hover{
            opacity: 1;
            background: linear-gradient(35deg, #43cea2 5%, #185a9d 65%);
            box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.5);
            color: #fff;
            span{
                &:before{
                    border-color: #fff;
                }
            }
        }
    }
}