/* layout
------------------------------------------------------------*/
body{
	font-family: $mainFont;
	font-size: 1.4rem;
	line-height: 1.5;
	background-color: #fff;
	color: #000;
	font-weight: 400;
	min-height: 100vh;
	overflow-x: hidden;

	@include sp {
		min-width: 320px;
	}
}

.l-cont {
	width: 100%;
	max-width: $baseWidth;
	margin-left: auto;
	margin-right: auto;

	@include pad {
		width: 90%;
	}
}

// -> 768px
@media screen and (min-width: #{($breakpoint1 + 1px)}) {
	.sp-only {
		display: none !important;
	}
}

// <- 1060px
@include pad {
	.pc-only {
		display: none !important;
	}
}

// -> 1061px
@include pc {
	.pad-sp-only {
		display: none !important;
	}

	a:hover, a:hover img {
		opacity: 0.80;
		filter: alpha(opacity=80);
		-ms-filter: "alpha(opacity=80)";
		text-decoration: none;
	}
}

section {
	padding: 50px 0;
}